import Vue from 'vue'
import Vuex from 'vuex'
import user from '@/store/modules/user'
import cart from '@/store/modules/cart'
Vue.use(Vuex)

export default new Vuex.Store({
  namespaced: true,
  state: {
    ///
    //selectAddress
    //index
    index: 0
  },
  getters: {
    token(state) {
      return state.user.info.token
    }
  },
  mutations: {
    setIndex(state, index) {
      state.index = index
    }
  },
  actions: {
  },
  modules: {
    user, cart
  }
})
